<template>
  <div>
    <generic-grid
      :params="params"
      :service="service"
      :headers="gridHeaders"
      :getDataCb="getDataCb"
      :toolbarTitle="toolbarTitle"
      :storeMutationKey="storeMutationKey"
    >
      <template v-slot:createForm="{ onSave, onClose, isOpen, isSaving }">
        <consumers-form
          v-if="isOpen"
          :stream="stream"
          :isEdit="false"
          :onSave="onSave"
          :onCancel="onClose"
          :isSaving="isSaving"
        ></consumers-form>
      </template>
      <template v-slot:editForm="{ onSave, item, onClose, isOpen, isSaving }">
        <consumers-form
          v-if="isOpen"
          :item="item"
          :isEdit="true"
          :onSave="onSave"
          :onCancel="onClose"
          :isSaving="isSaving"
        ></consumers-form>
      </template>
      <template v-slot:gridItemActions="{ item }">
        <icon-button
          color="nubeGreen"
          title="Writers"
          icon="mdi-link"
          :onClick="() => redirectTo(dialogNames.viewWriters, item)"
        ></icon-button>
      </template>
    </generic-grid>
  </div>
</template>
<script>
import ConsumersForm from './consumersForm.vue';
import IconButton from '../../Common/IconButton.vue';
import GenericGrid from '../../Common/GenericGrid.vue';
import { rfConsumersService } from '../../../services/rfConsumersService';

const dialogNames = {
  viewWriters: 'viewWriters',
};

const redirectMap = streamId => ({
  [dialogNames.viewWriters]: consumerId => `/rubix-flow-streams/${streamId}/consumers/${consumerId}/writers`,
});

export default {
  name: 'consumers-grid',
  components: { GenericGrid, ConsumersForm, IconButton },
  props: ['params', 'getDataCb'],
  data() {
    return {
      dialogNames: dialogNames,
      service: rfConsumersService,
      storeMutationKey: 'changeRfConsumers',
      toolbarTitle: 'Consumers',
      gridHeaders: [
        {
          text: 'Name',
          align: 'center',
          value: 'name',
          sortable: true,
        },
        {
          text: 'View',
          align: 'center',
          value: 'gridItemActions',
          sortable: false,
        },
        {
          text: 'Actions',
          align: 'center',
          value: 'actions',
          sortable: true,
        },
      ],
    };
  },
  computed: {
    stream() {
      return {
        uuid: this.params.uuid,
      };
    },
    computedRedirectMap() {
      return redirectMap(this.params.uuid);
    },
  },
  methods: {
    redirectTo(viewName, item) {
      const consumerId = item.uuid;
      if (!consumerId) {
        return;
      }

      let destination = this.computedRedirectMap[viewName];
      if (typeof destination === 'function') {
        this.$router.push({ path: destination(consumerId) });
      }
    },
  }
};
</script>
