<template>
  <v-card>
    <v-card-title>
      <v-btn @click="onBack" icon medium class="mr-1">
        <v-icon medium> mdi-arrow-left </v-icon>
      </v-btn>
      Consumers
    </v-card-title>

    <v-card-text>
      <breadcrumbs :items="breadcrumbItems"></breadcrumbs>
      <v-row no-gutter>
        <v-col cols="12" lg="12" xl="12" md="12" sm="12" xs="12">
          <consumers-grid v-if="params.uuid" :params="params" :getDataCb="getDataCb"> </consumers-grid>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>
<script>
import ConsumersGrid from './consumersGrid.vue';
import TooltipIconButton from '../../RubixApps/TooltipIconButton.vue';
import IconButton from '../../Common/IconButton.vue';
import Breadcrumbs from '../../Common/Breadcrumbs.vue';
import _get from 'lodash/get';
import routeConstant from '../../../constants/routes';

const baseHref = routeConstant.rfStreamsRoot;

export default {
  name: 'consumers',
  components: {
    IconButton,
    ConsumersGrid,
    TooltipIconButton,
    Breadcrumbs,
  },
  data() {
    return {
      params: {
        // fetch api flags
        uuid: null,
        consumers: true,
      },
      gridData: {},
      breadcrumbItems: [
        {
          text: 'Streams',
          disabled: false,
          link: baseHref,
        },
      ],
    };
  },
  mounted() {
    this.params = { ...this.params, uuid: this.$route.params.streamId };
  },
  methods: {
    onBack() {
      this.$router.go(-1);
    },
    getDataCb(data) {
      this.gridData = data;
      this.updateBreadCrumbs();
    },
    updateBreadCrumbs() {
      if (this.breadcrumbItems.length > 1) {
        return;
      }
      const streamUUID = _get(this.gridData, 'extraData.uuid', '');
      if (streamUUID) {
        this.breadcrumbItems.push({ text: streamUUID, align: 'center', disabled: true });
      }
    },
  },
};
</script>
